.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;

}
.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,
         transparent, 
         var(--color-primary), 
         transparent
    );
    display: grid;
    place-items: center;
}
.about__me-image{
    border-radius: 2rem;
    /* height: 100%; */
    overflow: hidden;
    transform: rotate(15deg);
    transition: var(--transition);


}
.about__me-image:hover{
    transform: rotate(0) 
}